import {
    SignOutAsync
  } from '../services/api';
export default {
    namespace: 'Home',
    state: {
      admittanceBody: {}
  
    },
    effects: {
      * getAdmittanceList ({ payload }, { call, put }) {
        const data = yield call(asset.getAdmittanceList, payload)
        yield put({ type: 'save', payload: { admittanceBody: data } })
      }
    },
    reducers: {
      save (state, action) {
        return { ...state, ...action.payload }
      }
    }
  }
      